<template>
  <main class="container flex">

    <!-- 1.st: VSTUP UIVATELE -->
    <section class="content flex">

      <!-- Komponenta pro nadpis -->
      <CipherHeader title="RSA" />

      <!-- Formul pro nastaven parametr -->
      <form class="params flex">
        <label>
          Prvoslo p:
          <input
            style="width: 84px"
            v-model.number="p"
            type="number"
            min="2"
            maxlength="7"
            @input="omezitDelku7($event, 'p')"
            @keydown="blokujNeciselnePismena"
            :class="{
              'invalid-border invalid-background':
                (p > 0 && !jePrvocislo(p)) ||
                (p > 0 && q > 0 && p === q) ||
                (p > 0 && q > 0 && p * q <= 90),
            }"
          />
        </label>
        <label>
          Prvoslo q:
          <input
            style="width: 84px"
            v-model.number="q"
            type="number"
            min="2"
            maxlength="7"
            @input="omezitDelku7($event, 'q')"
            @keydown="blokujNeciselnePismena"
            :class="{
              'invalid-border invalid-background':
                (q > 0 && !jePrvocislo(q)) ||
                (p > 0 && q > 0 && p === q) ||
                (p > 0 && q > 0 && p * q <= 90),
            }"
          />
        </label>
      </form>

      <!-- Komponenta pro textov vstup a vstup -->
      <CipherTextarea
        :vstupni-text="vstupniText"
        :vystupni-text="vystupniText"
        :custom-filter="customFilter"
        @keydown="handleBackspace"
      />

      <!-- Komponenta pro oznmen spnho zkoprovn textu (zptn vazba) -->
      <CipherToast :show="zobrazitToast" />

      <!-- Komponenta pro tlatka -->
      <CipherButtons 
      :disable-decrypt="vstupniText.length > 0 && !jeCislo(vstupniText)"
      :disable-encrypt="vstupniText.length > 0 && jeCislo(vstupniText)"
      />

      <!-- Oznamen uivateli, e parametr p,q nen validn (zptn vazba)  -->
      <section>
        <div v-if="(p > 0 && !jePrvocislo(p))">
          <p class="warning-color">ifrovn a deifrovn nen k dispozici!<br>Hodnota p nen prvoslo</p>
        </div>
        <div v-else-if="(q > 0 && !jePrvocislo(q))">
          <p class="warning-color">ifrovn a deifrovn nen k dispozici!<br>Hodnota q nen prvoslo</p>
        </div>
        <div v-else-if="(p > 0 && q > 0 && p === q)">
          <p class="warning-color">ifrovn a deifrovn nen k dispozici!<br>Hodnoty p a q mus bt rzn prvosla</p>
        </div>
        <div v-else-if="(p > 0 && q > 0 && p * q <= 90)">
          <p class="warning-color">ifrovn a deifrovn nen k dispozici!<br>Souin p a q mus bt vt ne 90</p>
        </div>
      </section>
    </section>

    <!-- 2.st: VIZUALIZACE -->
    <aside class="secondary-content flex">
      
      <!-- Cel postup rozdlen na 
       1.Vpoet kl  = spolene pro oteven text a ifrovan text
       2.ifrovn/Deifrovn = oddlen pro oteven text a ifrovan text -->
      <section
        class="rsa viz-step"
        :style="{
          height:
            (aktivniKrok === 'klice' && n > 0) ||
            (aktivniKrok === 'sifrovani' &&
              vstupniText &&
              n > 0)
              ? 'auto'
              : '120px',
        }"
      >

        <!-- Tlatka pepnn mezi vpotem kl a ifrovnm/deifrovnm -->
        <section class="tab-buttons flex">
          <button
            :class="[
              'tab-btn cursor-pointer',
              { active: aktivniKrok === 'klice' },
            ]"
            @click="aktivniKrok = 'klice'"
          >
            Vpoet kl
          </button>
          <button
            :class="[
              'tab-btn cursor-pointer',
              { active: aktivniKrok === 'sifrovani' },
            ]"
            @click="aktivniKrok = 'sifrovani'"
          >
            ifrovn / Deifrovn
          </button>
        </section>

        <!-- 1.Vpoet kl -->
        <section v-if="aktivniKrok === 'klice'">

          <!-- pokud jsou platn parametry p a q, ukzat postup pro vpoet kl -->
          <div v-if="n > 0" class="steps flex column" style="">

            <div class="step">
              <strong>1. Vpoet modulu n</strong>
              <p class="quaternary-color">
                n = p  q = {{ p }}  {{ q }} = {{ n }}
              </p>
            </div>

            <div class="step">
              <strong>2. Vpoet Eulerovy funkce (n)</strong>
              <p class="quaternary-color">
                (n) = (p-1)  (q-1) = {{ p - 1 }}  {{ q - 1 }} = {{ phi }}
              </p>
            </div>

            <div class="step">
              <strong>3. Volba veejnho exponentu e</strong>
              <div
                v-if="validniHodnotyE.length > 0"
                style="gap: 10px"
                class="flex"
              >
                <label>
                  e =
                  <select
                    v-model.number="e"
                    style="cursor: pointer; width: 100px"
                  >
                    <option
                      v-for="hodnota in validniHodnotyE"
                      :key="hodnota"
                      :value="hodnota"
                    >
                      {{ hodnota }}
                    </option>
                  </select>
                </label>
                <p class="quaternary-color">
                  mus splovat: nsd(e, (n)) = 1
                </p>
              </div>
            </div>

            <div class="step">
              <strong>4. Vpoet soukromho exponentu d</strong>
              <div
                v-if="validniHodnotyD.length > 0"
                style="gap: 10px"
                class="flex"
              >
                <label>
                  d =
                  <select
                    v-model.number="d"
                    style="cursor: pointer; width: 100px"
                  >
                    <option
                      v-for="hodnota in validniHodnotyD"
                      :key="hodnota"
                      :value="hodnota"
                    >
                      {{ hodnota }}
                    </option>
                  </select>
                </label>
                <p class="quaternary-color">
                  mus splovat: (e  d) mod (n) = 1
                </p>
              </div>
            </div>

            <div class="step">
              <strong>Veejn kl</strong>
              <p class="quaternary-color">(e, n) = ({{ e }}, {{ n }})</p>
            </div>

            <div class="step">
              <strong>Soukrom kl</strong>
              <p class="quaternary-color">(d, n) = ({{ d }}, {{ n }})</p>
            </div>
          </div>

          <!-- pokud jsou neplatn parametry p a q, ukzat zptnou vazbu -->
          <div v-else class="no-params quaternary-color">
            <p>
              Zadejte rzn prvosla p a q pro zobrazen krok.
            </p>
            <p>p  q mus bt vt ne 90</p>
          </div>

        </section>

        <!-- 2.ifrovn/Deifrovn -->
        <section v-if="aktivniKrok === 'sifrovani'">

          <!-- a) Deifrovn pro ifrovan text -->
          <div v-if="jeCislo(vstupniText) && n > 0" class="steps flex column">

            <div class="step" style="border-left: 3px solid #6e6a580d;">
              <strong>Vstupn text</strong>
              <p>
                <span
                  v-for="(znak, index) in vstupniText.split('')"
                  :key="index"
                  :class="{
                    'primary-color bold': index > posledniIndexMezery,
                  }"
                  >{{ znak }}</span
                >
              </p>
              <p>Aktuln ifrovan hodnota: c = {{ aktualniZnak.c }}</p>
            </div>


            <div class="step">
              <strong>Deifrovn</strong>
              <div v-if="aktualniZnak">
                <p>Pouit soukromho kle (d = {{ d }}, n = {{ n }})</p>
                <p class="courier-new quaternary-color">
                  m = c<sup>d</sup> mod n = {{ aktualniZnak.c
                  }}<sup>{{ d }}</sup> mod {{ n }} =
                  {{ aktualniZnak.mDesifrovane }}
                </p>
                <p>
                  Deifrovan hodnota: m = <span class=" primary-color bold">{{ aktualniZnak.mDesifrovane }}</span>
                </p>
              </div>
            </div>

            <div class="step" style="border-left: 3px solid #6e6a580d;">
              <strong>Pevod sla na znak</strong>
              <p>Kad slo jako ASCII hodnota</p>
              <div v-if="aktualniZnak">
                <p>
                  Aktuln slo: {{ aktualniZnak.mDesifrovane }} (znak = 
                  <span class="primary-color bold">{{ aktualniZnak.znakDesifrovany }}</span>)
                </p>
              </div>
            </div>

          </div>

          <!-- b) ifrovn pro oteven text -->
          <div v-else-if="vstupniText && n > 0" class="steps flex column">

            <div class="step" style="border-left: 3px solid #6e6a580d;">
              <strong>Vstupn text</strong>
              <p>
                <span
                  v-for="(znak, index) in vstupniText.split('')"
                  :key="index"
                  :class="{
                    'primary-color bold': index === vstupniText.length - 1,
                  }"
                  >{{ znak }}</span
                >
              </p>
            </div>

            <div class="step" style="border-left: 3px solid #6e6a580d;">
              <strong>Pevod znaku na slo</strong>
              <p>Kad znak jako ASCII hodnota</p>
              <div v-if="aktualniZnak">
                <p>
                  Aktuln znak: {{ aktualniZnak.znak }} (m =
                  <span class="primary-color bold">{{ aktualniZnak.m }}</span>)
                </p>
              </div>
            </div>

            <div class="step">
              <strong>ifrovn</strong>
              <div v-if="aktualniZnak">
                <p>Pouit veejnho kle (e = {{ e }}, n = {{ n }})</p>
                <p class="courier-new quaternary-color">
                  c = m<sup>e</sup> mod n = {{ aktualniZnak.m
                  }}<sup>{{ e }}</sup> mod {{ n }} = {{ aktualniZnak.c }}
                </p>
                <p>
                  ifrovan hodnota: c = 
                  <span class="primary-color bold">{{ aktualniZnak.c }}</span>
                </p>
              </div>
            </div>

          </div>

          <!-- pokud nejsou splnny podmnky pro zobrazen 2.ifrovn/Deifrovn, ukazat zptno uvazbu-->
          <div v-else class="no-params quaternary-color">
            <p>
              Zadejte text a rzn prvosla p a q pro zobrazen
              krok.
            </p>
            <p>p  q mus bt vt ne 90</p>
          </div>

        </section>

      </section>

    </aside>

    <!-- Komponenta pro zobrazen informac o ife -->
    <CipherInfo :show="zobrazitInfo">
      <RsaInfoContent />
    </CipherInfo>

  </main>
</template>

<script>
import {
  jePrvocislo,
  vypocitatNaPhi,
  najitValidniE,
  najitValidniD,
  rsaEncrypt,
  rsaDecrypt,
  ziskatAktualniZnak,
} from "@/utils/ciphers/rsa.js";
import RsaInfoContent from './components/RsaInfoContent.vue';

export default {
  name: "RsaCipherView",
  components: {
    RsaInfoContent
  },
  data() {
    return {
      vstupniText: "",
      vystupniText: "",
      zobrazitInfo: false,
      zobrazitToast: false,
      p: 0,
      q: 0,
      n: 0,
      phi: 0,
      e: 0,
      d: 0,
      aktivniKrok: "klice",
      validniHodnotyE: [],
      validniHodnotyD: [],
    };
  },
  methods: {
    sifrovat() {
      try {
        this.vystupniText = rsaEncrypt(this.vstupniText, this.e, this.n);
      } catch (error) {
        console.error("Dolo k chyb pi ifrovn:", error);
        alert("Chyba pi ifrovn, zkontrolujte parametry.");
      }
    },
    desifrovat() {
      try {
        this.vystupniText = rsaDecrypt(this.vstupniText, this.d, this.n);
      } catch (error) {
        console.error("Dolo k chyb pi deifrovn:", error);
        alert("Chyba pi deifrovn, zkontrolujte parametry.");
      }
    },
    kopirovat(text) {
      if (text !== "") {
        navigator.clipboard.writeText(text);
        this.zobrazitToast = true;
        setTimeout(() => {
          this.zobrazitToast = false;
        }, 2000);
      }
    },
    vymazatVse() {
      this.vstupniText = "";
      this.vystupniText = "";
      this.p = 0;
      this.q = 0;
      this.n = 0;
      this.phi = 0;
      this.e = 0;
      this.d = 0;
    },
    blokujNeciselnePismena(udalost) {
      const zakazaneKlavesy = ['e', 'E', '+', '-', '.', ','];
      if (zakazaneKlavesy.includes(udalost.key)) {
        udalost.preventDefault();
      }
    },
    // fce pro mazan celch ifrovanch hodnot, kdy mau na vstupu zaifrovan znaky (tedy sla oddlen mezerami)
    handleBackspace(event) {
      if (event.key === 'Backspace' && this.jeCislo(this.vstupniText)) {
        const textarea = event.target;
        const cursorPos = textarea.selectionStart;
        const text = this.vstupniText;
        
        if (cursorPos === text.length) {
          event.preventDefault(); // preventDefault() zastav vchoz chovn udlosti (backspace pro nas)
          
          const posledniMezera = text.lastIndexOf(' '); 
          
          if (posledniMezera !== -1) {
            this.vstupniText = text.substring(0, posledniMezera); // substring(0, posledniMezera) vezme text od zaatku a k posledn mezee (smae posledni blok sel)
          } else {
            this.vstupniText = '';
          }
        }
      }
    },
    jeCislo(text) {
      if (!text) return false;
      const prvniZnak = text.trim().charAt(0); 
      return prvniZnak >= "0" && prvniZnak <= "9";
    },
    jePrvocislo(num) {
      return jePrvocislo(num);
    },
    // vypotn vech potebnch parametr(n,phi,e,d) na zaklad vstupnch p a q, ktere jsou ve WATCHERS a vdy se tato funkce vola pi jejich zmn
    vypocitatRSAParametry() {
      const vysledek = vypocitatNaPhi(this.p, this.q);
      
      if (vysledek) {
        this.n = vysledek.n;
        this.phi = vysledek.phi;
        this.validniHodnotyE = najitValidniE(this.phi);

        if (this.validniHodnotyE.length > 0) {
          if (!this.validniHodnotyE.includes(this.e)) {
            this.e = this.validniHodnotyE[0];
          }
        } else {
          this.e = 0;
        }

        this.vypocitatValidniD();
      } else {
        // vyresetovat hodnoty pokud nespluj podmnky
        this.n = 0;
        this.phi = 0;
        this.e = 0;
        this.d = 0;
        this.validniHodnotyE = [];
        this.validniHodnotyD = [];
      }
    },
    // prvnch 7 nejmench d
    vypocitatValidniD() {
      this.validniHodnotyD = najitValidniD(this.e, this.phi);

      if (this.validniHodnotyD.length > 0) {
        this.d = this.validniHodnotyD[0];
      }
    },
    // omezeni vstupu p a q
    omezitDelku7(event, field) {
      const value = event.target.value;
      if (value.length > 7) {
        this[field] = parseInt(value.slice(0, 7));
      }
    },
  },
  // WATCH: sleduje zmny konkrtn reaktivn promnn a spust funkci, kdy se zmn
  watch: {
    p() {
      this.vypocitatRSAParametry();
    },
    q() {
      this.vypocitatRSAParametry();
    },
    e() {
      this.vypocitatValidniD();
    },
  },
  // COMPUTED: vypotan hodnota, kter se automaticky pepot, jen kdy se zmn data, na kterch zvis
  computed: {
    // custom filtr pro rsa oteven text znaky,mezery,teky a ifrovan text jsou sla oddlen mezerami
    customFilter() {
      return (hodnota) => {
        const prvniZnak = hodnota.charAt(0);

        if (prvniZnak >= "0" && prvniZnak <= "9") {
          let filtrovanaHodnota = hodnota.replace(/[^\d\s]/g, "");  // odstran ve krom sel a mezer
          filtrovanaHodnota = filtrovanaHodnota.replace(/\s{2,}/g, " ");  // nahrad vce mezer za sebou jednou mezerou
          return filtrovanaHodnota;
        } else {
          const filtrovanaHodnota = hodnota.replace(/[^a-zA-Z\s.]/g, ""); // povoleny psmena, mezery a teky
          return filtrovanaHodnota.toUpperCase();
        }
      };
    },
    aktualniZnak() {
      return ziskatAktualniZnak(this.vstupniText, this.e, this.d, this.n);
    },
    posledniIndexMezery() {
    if (!this.vstupniText) return -1;
    const posledniMezera = this.vstupniText.lastIndexOf(' '); 
    // pokud nen mezera, zvrazn cel text (vrt -1)
    return posledniMezera;
    },
  },
};
</script>

<style scoped>
@import "@/assets/styles/cipher/rsa.css";
</style>
