<template>
  <div>
    <h3 id="info-title">O Afinn ife</h3>
    <p>
      Afinn ifra je
      <router-link
        to="/#monoalfabeticka"
        class="primary-color without-underline"
      >
        monoalfabetick ifra </router-link
      >, kter pouv linern funkci k ifrovn a deifrovn textu. Pro
      sprvn fungovn mus bt hodnota A nesoudln s 26.
    </p>
    <h4>Popis</h4>
    <p>
      ifru si mete pedstavit jako vylepenou Caesarovu ifru, kter
      pouv dva kle (dv hodnoty) msto jednoho. Nazvme je hodnota A a
      hodnota B.
    </p>
    <p>
      Hodnota B (Posun): Tato hodnota funguje pesn jako u Caesarovy
      ifry, jednodue posune kad psmeno v abeced o dan poet mst.
      Napklad pokud je B=3, "A" se stane "D", "B"se stane "E" atd.
    </p>
    <p>
      Hodnota A (Nsoben): Toto je ten hlavn rozdl. Ne se psmeno posune
      pomoc hodnoty B, jeho pozice v abeced se nejprve peskld podle
      hodnoty A.
    </p>
    <p>
      Kad psmeno v pvodnm textu je pevedeno na svj index v abeced (0
      pro A, 1 pro B atd.). Pot se pouije vzorec pro ifrovn:
    </p>
    <h5>Vzorec pro ifrovn</h5>
    <pre
      style="
        white-space: pre-wrap;
        font-size: 1em;
        font-weight: bold;
        color: #36a06f;
        margin-bottom: -1em;
      "
    >
C = (A  P + B) MOD 26
    </pre>
    <p>
      Kde C je index ifrovanho psmene, P je index pvodnho psmene, A je
      kl ifry a B je posun. Pro deifrovn se pouv inverzn hodnota A
      (A):
    </p>
    <h5>Vzorec pro deifrovn</h5>
    <pre
      style="
        white-space: pre-wrap;
        font-size: 1em;
        font-weight: bold;
        color: #36a06f;
        margin-bottom: -1em;
      "
    >
P = (A  (C - B)) MOD 26
    </pre>
    <p>
      Kde P je index pvodnho psmene, C je index ifrovanho psmene, A je
      inverzn hodnota A a B je posun.
    </p>
    <h5>A je hodnota, kter spluje:</h5>
    <pre
      style="
        white-space: pre-wrap;
        font-size: 1em;
        font-weight: bold;
        color: #36a06f;
        margin-bottom: -1em;
      "
    >
A  A MOD 26 = 1
    </pre>
    <h4>Bezpenost</h4>
    <p>
      Afinn ifra je relativn slab, protoe existuje pouze 12 monch
      hodnot A, kter jsou nesoudln s 26. Proto existuje 1226 = 312 rznch
      kl. To je stle velmi mlo, protoe tonk me snadno vyzkouet
      vechny kombinace a tm prolomit ifru
      <router-link to="/#brute-force" class="primary-color without-underline"
        >brute-force</router-link
      >.
    </p>
    <h4>Vce Informac</h4>
    <a
      href="https://en.wikipedia.org/wiki/Affine_cipher"
      target="_blank"
      class="primary-color without-underline"
      >Wikipedia: Affine cipher</a
    >
    <h4>Test</h4>
    <p>
      Zkuste deifrovat nsledujc zprvu pomoc hodnoty B = 8:
      <strong>ZCUZ</strong>
    </p>
    <details>
      <summary>Odpov</summary>
      <p>Pvodn text je <strong>TEST</strong></p>
    </details>
  </div>
</template>

<script>
export default {
  name: 'AffineInfoContent'
}
</script>
