<!-- Komponenta pro informace o DES (Data Encryption Standard) -->
<template>
  <div>
    <h3 id="info-title">O DES (Data Encryption Standard)</h3>
    
    <p>
      DES je blokov ifra vyvinut spolenost IBM v 70. letech 20. stolet.
      V roce 1977 byla schvlena americkou vldou jako federln standard pro
      ifrovn citlivch dat. Dnes je povaovna za zastaralou a nezabezpeenou
      kvli sv krtk dlce kle. Byla nahrazena modernjmi iframi, jako je
      <router-link to="/#aes" class="primary-color without-underline">
        AES (Advanced Encryption Standard)
      </router-link>.
    </p>

    <h4>Popis</h4>
    <p>
      DES je zaloena na <span class="primary-color">Feistelov ife</span> a pracuje s bloky dat o velikosti 64 bit.
      je to symetrick blokov ifra, kter pouv kl o dlce 56 bit
      (+ 8 paritnch bit). ifrovn probh v 16 rundch, kde kad
      runda pouv vlastn rundovn kl o dlce 48 bit. Pro jednoduchost se v na aplikaci
      pouv pouze jedna a to prvn runda.
    </p>

    <h5>Zkladn charakteristiky</h5>
    <p>Typ: <router-link to="/#symetricke" class="primary-color without-underline">Symetrick</router-link>, blokov, bitov orientovan ifra<br>
    Dlka bloku: 64 bit<br>
    Dlka kle: 56 bit (+ 8 paritnch bit = 64 bit celkem)<br>
    Poet rund: 16<br>
    Dlka rundovnch kl: 48 bit</p>

    <h5>Postup ifrovn</h5>
    <ol>
      <li>
        Poten permutace:
        Vstupn blok 64 bit se permutuje podle tabulky <span class="primary-color">IP</span>
      </li>
      <li>
        Rozdlen:
        Permutovan blok se rozdl na levou <span class="tertiary-color">L<sub>0</sub></span> a pravou <span class="tertiary-color">R<sub>0</sub></span> polovinu po 32 bitech
      </li>
      <li>
        16 rund: Pro kadou rundu i = 1 a 16:
        <span class="tertiary-color">L<sub>i</sub> = R<sub>i-1</sub></span> a 
        <span class="tertiary-color">R<sub>i</sub> = L<sub>i-1</sub>  f(k<sub>i</sub>, R<sub>i-1</sub>)</span>
      </li>
      <li>
        Konen permutace:
        Spoj se <span class="tertiary-color">R<sub>16</sub></span> a <span class="tertiary-color">L<sub>16</sub></span> a aplikuje se inverzn poten permutace <span class="primary-color">IP<sup>-1</sup></span>
      </li>
    </ol>

    <h5>Rundovn funkce <span class="tertiary-color">f(k, R)</span></h5>
    <ol>
      <li>
        Expanzn permutace <span class="primary-color">E</span>:
        Rozen 32 bit prav poloviny na 48 bit
      </li>
      <li>
        XOR s rundovnm klem:
        <span class="primary-color">E(R)  k<sub>i</sub></span>
      </li>
      <li>
        <span class="primary-color">S-boxy</span>:
        8 substitunch box, kad pevd 6 bit na 4 bity
      </li>
      <li>
        <span class="primary-color">P-box</span>:
        Permutace vslednch 32 bit
      </li>
    </ol>

    <h5>Generovn rundovnch kl (k<sub>i</sub>)</h5>
    <ol>
      <li>Z 64-bitovho kle se odeberou paritn bity</li>
      <li>Zbylch 56 bit se permutuje pomoc permutace <span class="primary-color">PC-1</span></li>
      <li>Kl se rozdl na <span class="tertiary-color">C<sub>0</sub></span> a <span class="tertiary-color">D<sub>0</sub></span> (kad po 28 bitech)</li>
      <li>
        Pro kadou rundu i: <span class="tertiary-color"> C<sub>i</sub> = RL<sub>i</sub>(C<sub>i-1</sub>)</span> 
        a <span class="tertiary-color">D<sub>i</sub> = RL<sub>i</sub>(D<sub>i-1</sub>)</span>,<br>
        kde RL<sub>i</sub> je rotace doleva o 1 bit (pro rundy 1, 2, 9, 16) nebo 2 bity (ostatn rundy)
      </li>
      <li>Spoj se <span class="tertiary-color">C<sub>i</sub></span> a <span class="tertiary-color">D<sub>i</sub></span> a aplikuje se permutace <span class="primary-color">PC-2</span>
          pro zskn 48-bitovho rundovnho kle <span class="primary-color">k<sub>i</sub></span></li>
    </ol>

    <h5>Deifrovn</h5>
    <p>
      DES pouv stejn algoritmus pro deifrovn jako pro ifrovn dky
      Feistelov struktue. Jedin rozdl je v poad aplikace rundovnch
      kl, pi deifrovn se pouvaj obrcen (k<sub>16</sub> a k<sub>1</sub>).
    </p>

    <h4>Bezpenost</h4>
    <p>
      Poet monch kl je piblin 72 kvadrilion. I kdy se to zd jako velk slo,
      modern potae a distribuovan systmy mohou tento prostor kl prohledat v
      rozumnm ase. V roce 1998 byl DES prolomen za mn ne 3 dny pomoc specializovanho hardwaru.
      DES je dnes povaovn za nezabezpeen kvli krtk
      dlce kle - lze prolomit <router-link to="/#brute-force" class="primary-color without-underline">brute-force</router-link>.
    </p>

    <h4>Vce informac</h4>
    <a
      href="https://en.wikipedia.org/wiki/Data_Encryption_Standard"
      target="_blank"
      class="primary-color without-underline"
    >
      Data Encryption Standard - Wikipedia
    </a>

    <h4>Test</h4>
    <p>
      Jak je pvodn text, pokud mme zaifrovan blok
      0000110001011101000101100101000101010110010001110001000101010100<br>
      i odpovdajc kl
      <strong>SECURITY</strong>?
    </p>
    <details>
      <summary>Odpov</summary>
      <p><strong>MYSECRET</strong></p>
    </details>
  </div>
</template>

