<!-- Komponenta pro informace o Vigenrov ife -->
<template>
  <div>
    <h3 id="info-title">O Vigenrov ife</h3>
    <p>
      ifra vychz<router-link
        to="/#alberti-h"
        class="primary-color without-underline"
      >
        z ifry </router-link
      >florentskho polyhistora Leona Battisty Albertiho.
      <router-link to="/#vigenere-h" class="primary-color without-underline">
        Vigenrova ifra</router-link
      >
      je
      <router-link
        to="/#polyalfabeticka"
        class="primary-color without-underline"
        >polyalfabetick ifra</router-link
      >, kter pouv kl k uren posunu pro kad psmeno v textu. ifra
      je pojmenovna po francouzskm diplomatovi Blaise de Vigenre, kter ji
      popularizoval v 16. stolet.
    </p>
    <h4>Popis</h4>
    <p>
      Vigenre ifra pouv kl, kter se opakuje pro kad psmeno v textu.
      Kad psmeno v kli uruje posun pro odpovdajc psmeno v textu.
      Napklad, pokud je kl "KEY" a text "HELLO", prvn psmeno "H" bude
      posunuto o hodnotu "K", druh "E" o hodnotu "E", atd.
    </p>
    <p>
      Kad psmeno v pvodnm textu je pevedeno na svj index v abeced (0
      pro A, 1 pro B atd.). Pot se pouije vzorec pro ifrovn:
    </p>
    <h5>Vzorec pro ifrovn</h5>
    <pre
      style="
        white-space: pre-wrap;
        font-size: 1em;
        font-weight: bold;
        color: #36a06f;
      "
    >
C = (P + K) MOD 26</pre
    >
    <p>
      Kde C je index ifrovanho psmene, P je index pvodnho psmene a K je
      index psmene v kli.
    </p>
    <h5>Vzorec pro deifrovn</h5>
    <pre
      style="
        white-space: pre-wrap;
        font-size: 1em;
        font-weight: bold;
        color: #36a06f;
      "
    >
P = (C - K + 26) MOD 26</pre
    >
    <p>
      Kde P je index pvodnho psmene, C je index ifrovanho psmene a K je
      index psmene v kli.
    </p>
    <h4>Bezpenost</h4>
    <p>
      Existuje 26 na m-tou rznch kl dlky m, ale stle nen bezpen.
      Vigenre ifra byla povaovna za velmi bezpenou a do 19. stolet, kdy
      byly vyvinuty metody k prolomen ifry, jako je
      <router-link
        to="/#kasiskeho-test"
        class="primary-color without-underline"
        >Kasiskiho test</router-link
      >
      a
      <router-link
        to="/#fridmanuv-test"
        class="primary-color without-underline"
        >Friedmanv test</router-link
      >. Dnes je Vigenrova ifra povaovna za slabou a nen doporuovna pro
      bezpenou komunikaci.
    </p>
    <h4>Vce informac</h4>
    <a
      href="https://en.wikipedia.org/wiki/Vigen%C3%A8re_cipher"
      target="_blank"
      class="primary-color without-underline"
      >Vigenre cipher - Wikipedia</a
    >
    <h4>Test</h4>
    <p>
      Jak je kl pro ifrovan text <strong>DIOIETXK</strong>, kdy pvodn
      text je <strong>SECURITY</strong>? Kl m 5 znak a je tvoen pouze
      psmeny.
    </p>
    <details>
      <summary>Odpov</summary>
      <p>Kl je <strong>LEMON</strong>.</p>
    </details>
  </div>
</template>

<script>
export default {
  name: 'VigenereInfoContent'
}
</script>
