/**
 * Afinn ifra
 */

const ALPHABET = "ABCDEFGHIJKLMNOPQRSTUVWXYZ";

/**
 * Inverse sla: a modulo 26
 * @param {number} a - kl a
 * @returns {number|null} - inverze nebo null, pokud neexistuje
 */
function inverseA(a) {
    for (let x = 1; x < 26; x++) {
        if ((a * x) % 26 === 1) {
            return x;
        }
    }
    return null;
}

/**
 * ENCRYPT
 * @param {string} text Oteven text
 * @param {number} a kl a, mus bt nesoudln s 26 (m inverzi modulo 26)
 * @param {number} b kl b
 * @returns {string} Zaifrovan text
 * @throws {Error} Pokud a nen nesoudln s 26
 */
export function encrypt(text, a, b) {
    if (inverseA(a) === null) {
        throw new Error("Chyba ifrovn: A mus bt nesoudln s 26");
    }

    let result = "";

    for (let char of text) {
        if (ALPHABET.includes(char)) {
            const p = ALPHABET.indexOf(char);
            const c = (a * p + b) % 26;
            result += ALPHABET[c];
        } else if (char === ' ' || char === '.') {
            result += char;
        }
    }

    return result;
}

/**
 * DECRYPT
 * @param {string} text Zaifrovan text
 * @param {number} a kl a, mus bt nesoudln s 26 (m inverzi modulo 26)
 * @param {number} b kl b
 * @returns {string} Deifrovan text
 * @throws {Error} Pokud a nen nesoudln s 26
 */
export function decrypt(text, a, b) {
    const aInverse = inverseA(a);

    if (aInverse === null) {
        throw new Error("Chyba deifrovn: A mus bt nesoudln s 26");
    }

    let result = "";


    for (let char of text) {
        if (ALPHABET.includes(char)) {
            const c = ALPHABET.indexOf(char);
            const p = (aInverse * (c - b + 26)) % 26;
            result += ALPHABET[p];
        } else if (char === ' ' || char === '.') {
            result += char;
        }
    }

    return result;
}
