<template>
  <div>
    <h3 id="info-title">O Posunn ife</h3>
    <p>
      Posunn ifra, nebo tak znm jako
      <router-link to="/#caesarova" class="primary-color without-underline"
        >Caesarova ifra</router-link
      >. je pojmenovna po mskm vojenskm a politickm vdci Gaiu Juliu
      Caesarovi, kter tuto jednoduchou ifru pouval pro ifrovn
      vojenskch zprv. Je to
      <router-link
        to="/#monoalfabeticka"
        class="primary-color without-underline"
        >monoalfabetick ifra</router-link
      >.
    </p>
    <h4>Popis</h4>
    <p>
      Posuvn ifra je typ ifry, kde je kad znak v otevenm textu nahrazen
      znakem, kter je posunut o pevn poet mst v abeced. Napklad pi
      posunu o 3 se psmeno "A" stane "D", "B" se stane "E", a tak dle. Po
      dosaen konce abecedy se pokrauje od zatku (nap. "Z" se stane "C").
    </p>
    <p>
      Kad psmeno v pvodnm textu je pevedeno na svj index v abeced (0
      pro A, 1 pro B atd.). Pot se pouije vzorec pro ifrovn:
    </p>
    <h5>Vzorec pro ifrovn</h5>
    <pre
      style="
        white-space: pre-wrap;
        font-size: 1em;
        font-weight: bold;
        color: #36a06f;
      "
    >
C = (P + K) MOD 26</pre
    >
    <p>
      Kde C je index ifrovanho psmene, P je index pvodnho psmene a K je
      hodnota posunu (kl).
    </p>
    <h5>Vzorec pro deifrovn</h5>
    <pre
      style="
        white-space: pre-wrap;
        font-size: 1em;
        font-weight: bold;
        color: #36a06f;
      "
    >
P = (C - K + 26) MOD 26</pre
    >
    <p>
      Kde P je index pvodnho psmene, C je index ifrovanho psmene a K je
      hodnota posunu (kl).
    </p>
    <h4>Bezpenost</h4>
    <p>
      Poet monch kl odpovd velikosti pouit abecedy. Pro 26 psmen je
      tedy 26 kl, ale 26. kl je bezvznamn (kad psmeno se mapuje samo
      na sebe). Ve skutenosti je tedy pouze 25 smysluplnch kl, to
      umouje snadn prolomen ifry vyzkouenm vech monost
      <router-link to="/#brute-force" class="primary-color without-underline"
        >brute-force</router-link
      >.
    </p>
    <h4>Vce informac</h4>
    <a
      href="https://en.wikipedia.org/wiki/Caesar_cipher"
      target="_blank"
      class="primary-color without-underline"
    >
      Wikipedia: Caesar cipher
    </a>
    <h4>Test</h4>
    <p>
      Jak je pvodn text pro ifrovan text
      <strong>KHOOR ZRUOG</strong>?
    </p>
    <details>
      <summary>Odpov</summary>
      <p>Pvodn text je <strong>HELLO WORLD</strong></p>
    </details>
  </div>
</template>

