/**
 * Vigenerova ifra 
 */

const ALPHABET = "ABCDEFGHIJKLMNOPQRSTUVWXYZ";

/**
 * ENCRYPT
 * @param {string} text Oteven text
 * @param {string} key kl
 * @returns {string} Zaifrovan text
 */
export function encrypt(text, key) {
    let result = "";
    let keyIndex = 0;

    for (let char of text) {
        if (ALPHABET.includes(char)) {
            const keyChar = key[keyIndex % key.length];

            const textIndex = ALPHABET.indexOf(char);
            const keyCharIndex = ALPHABET.indexOf(keyChar);

            const newIndex = (textIndex + keyCharIndex) % 26;
            result += ALPHABET[newIndex];
            keyIndex++;
        } else if (char === ' ' || char === '.') {
            result += char;
        }
    }

    return result;
}

/**
 * DECRYPT
 * @param {string} text Zaifrovan text
 * @param {string} key kl
 * @returns {string} Deifrovan text
 */
export function decrypt(text, key) {
    let result = "";
    let keyIndex = 0;

    for (let char of text) {
        if (ALPHABET.includes(char)) {
            const keyChar = key[keyIndex % key.length];

            const textIndex = ALPHABET.indexOf(char);
            const keyCharIndex = ALPHABET.indexOf(keyChar);

            const newIndex = (textIndex - keyCharIndex + 26) % 26;
            result += ALPHABET[newIndex];
            keyIndex++;
        } else if (char === ' ' || char === '.') {
            result += char;
        }
    }

    return result;
}
