/**
 * Substitun ifra
 */

const ALPHABET = "ABCDEFGHIJKLMNOPQRSTUVWXYZ";

/**
 * ENCRYPT
 * @param {string} text Oteven text
 * @param {string} permutation Permutace anglick abecedy
 * @returns {string} Zaifrovan text
 */
export function encrypt(text, permutation) {
    let result = "";

    for (let char of text) {
        if (ALPHABET.includes(char)) {
            const position = ALPHABET.indexOf(char);
            const encryptedChar = permutation[position];
            result += encryptedChar;
        } else if (char === ' ' || char === '.') {
            result += char;
        }
    }

    return result;
}

/**
 * DECRYPT
 * @param {string} text Zaifrovan text
 * @param {string} permutation Permutace anglick abecedy
 * @returns {string} Deifrovan text
 */
export function decrypt(text, permutation) {
    let result = "";

    for (let char of text) {
        if (permutation.includes(char)) {
            const position = permutation.indexOf(char);
            const decryptedChar = ALPHABET[position];
            result += decryptedChar;
        } else if (char === ' ' || char === '.') {
            result += char;
        }
    }

    return result;
}
