
<!-- Komponenta pro informace o Substitun ife -->
<template>
  <div>
    <h3 id="info-title">O Substitun ife</h3>
    <p>
      Substitun ifra je
      <router-link
        to="/#monoalfabeticka"
        class="primary-color without-underline"
        >monoalfabetick ifra</router-link
      >, kter nahrazuje kad psmeno v textu jinm psmenem. Pro sprvn
      fungovn mus bt kl (nhrada psmen) pevn stanoven.
    </p>
    <h4>Popis</h4>
    <p>
      V tto ife je kad psmeno abecedy nahrazeno jinm psmenem podle
      pedem uren permutace. Napklad, pokud je permutace
      "QWERTYUIOPASDFGHJKLZXCVBNM", pak psmeno "A" bude nahrazeno "Q", "B"
      bude nahrazeno "W", a tak dle.
    </p>
    <h4>Bezpenost</h4>
    <p>
      Existuje 26! monch permutac pro anglickou
      abecedu, co in tuto ifru relativn bezpenou proti hrub sle.
      Nicmn, je nchyln k
      <router-link
        to="/#frekvencni-analyza"
        class="primary-color without-underline"
        >frekvenn analze</router-link
      >, protoe nkter psmena se v jazyce vyskytuj astji ne jin.
    </p>
    <h4>Vce Informac</h4>
    <a
      href="https://en.wikipedia.org/wiki/Substitution_cipher"
      target="_blank"
      class="primary-color without-underline"
      >Wikipedia: Substitution cipher</a
    >
    <h4>Test</h4>
    <p>
      Pi permutaci <strong>MBNAOXFTEJSQLWHRVCDPUGIYKZ</strong> jak psmeno
      nahrad <strong>A</strong>? Pi stejn permutaci jak psmeno nahrad
      <strong>H</strong>? Pokud je ifrovan text
      <strong>ROCLUPMPEHW</strong>, jak je pvodn text?
    </p>
    <details>
      <summary>Odpov</summary>
      <p>
        A  <strong>M</strong>, H  <strong>T</strong>, pvodn text je<strong>
          PERMUTATION</strong
        >
      </p>
    </details>
  </div>
</template>

