<template>
  <main class="container flex">

    <!-- 1.st: VSTUP UIVATELE -->
    <section class="content flex">

      <!-- Komponenta pro nadpis -->
      <CipherHeader title="Posuvn ifra" />

      <!-- Formul pro nastaven parametr -->
      <form class="params flex">
        <label for="shift-slider">Zvolte kl ifry:</label>
        <input
          type="range"
          class="form-range"
          min="0"
          max="25"
          step="1"
          v-model.number="posun"
          @input="otocitKolo"
        />
        <input
          type="number"
          min="0"
          max="25"
          v-model.number="posun"
          class="input"
          @keydown="blokujNeciselnePismena"
        />
      </form>

      <!-- Komponenta pro textov vstup a vstup -->
      <CipherTextarea
        :vstupni-text="vstupniText"
        :vystupni-text="vystupniText"
      />

      <!-- Komponenta pro oznmen spnho zkoprovn textu (zptn vazba) -->
      <CipherToast :show="zobrazitToast" />

      <!-- Komponenta pro tlatka -->
      <CipherButtons />

    </section>

    <!-- 2.st: VIZUALIZACE -->
    <aside class="secondary-content flex">
      <img alt="Vnj kolo" class="wheel outer" />
      <img
        alt="Vnitn kolo"
        class="wheel inner"
        :style="{ transform: 'rotate(' + uhelKola + 'deg)' }"
      />
    </aside>

    <!-- Komponenta pro zobrazen informac o ife -->
    <CipherInfo :show="zobrazitInfo">
      <CaesarInfoContent />
    </CipherInfo>

  </main>
</template>

<script>
import { encrypt, decrypt } from '@/utils/ciphers/caesar';
import CaesarInfoContent from './components/CaesarInfoContent.vue';
export default {
  name: "CaesarView",
  components: {
    CaesarInfoContent
  },
  data() {
    return {
      posun: 0,
      vstupniText: "",
      vystupniText: "",
      uhelKola: 75,
      zobrazitInfo: false,
      zobrazitToast: false,
    };
  },
  methods: {
    async sifrovat() {
      try {
        this.vystupniText = encrypt(this.vstupniText, this.posun);
      } catch (error) {
        console.error("Dolo k chyb pi ifrovn:", error);
        alert("Chyba pi ifrovn, zkontrolujte parametry");
      }
    },
    async desifrovat() {
      try {
        this.vystupniText = decrypt(this.vstupniText, this.posun);
      } catch (error) {
        console.error("Dolo k chyb pi deifrovn:", error);
        alert("Chyba pi deifrovn, zkontrolujte parametry");
      }
    },
    kopirovat(text) {
      if (text !== "") {
        navigator.clipboard.writeText(text);
        this.zobrazitToast = true;
        setTimeout(() => {
          this.zobrazitToast = false;
        }, 2000);
      }
    },
    vymazatVse() {
      this.vstupniText = "";
      this.vystupniText = "";
      this.posun = 0;
      this.uhelKola = 75;
    },
    blokujNeciselnePismena(udalost) {
      const zakazaneKlavesy = ['e', 'E', '+', '-', '.', ','];
      if (zakazaneKlavesy.includes(udalost.key)) {
        udalost.preventDefault();
      }
    },
    // oten kola pi zmn posunu (kle)
    otocitKolo() {
      const korekce = 75;
      this.uhelKola = this.posun * (360 / 26) + korekce;
    },
  },
  // WATCH: sleduje zmny konkrtn reaktivn promnn a spust funkci, kdy se zmn
  watch: {
    posun() {
      if (this.posun < 0) {
        this.posun = 0;
      }
      if (this.posun > 25) {
        this.posun = 25;
      }
      this.otocitKolo();
    }
  },
};
</script>

<style scoped>
@import "@/assets/styles/cipher/caesar.css";
</style>
