<!-- Komponenta pro informace o Proudov ife -->
<template>
  <div>
    <h3 id="info-title">O Proudov ife</h3>
    <p>
      Proudov ifra je typ ifrovacho algoritmu, kter ifruje data po
      bitech nebo bajtech. Pro kzku si vysvtlme jak funguje proudov ifra
      zaloen na linear feedback shift registru (LFSR). Tato metoda
      generovn klovho proudu, byla dve asto vyuvna ve vojenskch
      ifrch.
    </p>
    <h4>Popis</h4>
    <p>
      Proudov ifry generuj nekonen pseudonhodn proud bit, kter je
      kombinovn s otevenm textem pomoc operace XOR k vytvoen ifrovanho
      textu. Tento proud bit je v naem ppad generovn pomoc linernho
      zptnovazebnho posuvnho registru (LFSR), kter vyuv poten
      vektor a koeficienty k uren svho stavu.
    </p>
    <h5>Parametry</h5>
    <p>
      <span class="primary-color">m</span>: Dlka registru (poet bit). Stejnou dlku mus mt
      poten vektor a koeficienty.
    </p>
    <p>
      <span class="primary-color">Poten vektor</span>: Poten stav registru,
      reprezentovan jako binrn etzec.
    </p>
    <p>
      <span class="primary-color">Koeficienty</span>: Uruj, kter bity registru se pouvaj k
      vpotu zptn vazby.
    </p>
    <p>
      <span class="primary-color"></span>: Operace XOR, kter se pouv k vpotu zptn
      vazby, ale i k ifrovn/deifrovn dat. Hodnota je 1, prv kdy poet
      vstup s hodnotou 1 je lich.
    </p>
    <h5>Popis generovn proudu kl</h5>
    <p>
      Generovn proudu kl zan s potenm vektorem, kter uruje
      poten stav registru. Na kadm kroku se vypot nov bit zptn
      vazby pomoc XOR operace () na bitech urench koeficienty. Tento nov
      bit je pidn na zatek registru a nejstar bit je odstrann. Vstupn
      bit (nejstar bit registru) se pouv jako dal bit proudu kl.
    </p>
    <h5>Vzorec pro ifrovn/deifrovn</h5>
    <pre
      style="
        white-space: pre-wrap;
        font-size: 1em;
        font-weight: bold;
        color: #36a06f;
      "
    >
C = P  K</pre
    >
    <p>
      Kde C je ifrovan text, P je oteven text, K je proud kl a 
      oznauje operaci XOR. C a P lze prohodit a deifrovat tak ifrovan
      text.
    </p>
    <h4>Bezpenost</h4>
    <p>
      Bezpenost proudovch ifer zvis na kvalit genertoru pseudonhodnch
      sel a sprvnm pouit potenho vektoru a koeficient. Pokud jsou
      tyto parametry slab nebo opakovan pouvny, me bt ifra nchyln k
      tokm.
    </p>
    <h4>Vce informac</h4>
    <a
      href="https://en.wikipedia.org/wiki/Stream_cipher"
      target="_blank"
      class="primary-color without-underline"
      >Stream cipher - Wikipedia</a
    >
    <h4>Test</h4>
    <p>
      <strong>1.</strong> Mjme registr m=4, poten stav 1010 a koeficienty
      1001. Jak bude <strong>prvn bit proudu kl</strong> a jak bude
      <strong>nov stav registru</strong> po jednom kroku?
    </p>
    <details>
      <summary>Odpov 1</summary>
      <p>
        <strong>Prvn bit proudu kl:</strong> 0 (posledn bit z registru
        101<strong>0</strong>).
      </p>
      <p><strong>Nov stav registru:</strong>1101.</p>
      <p>
        Zptn vazba se pot z bit, kde je koeficient '1' (prvn a tvrt
        pozice). Tedy <strong>1</strong>01<strong>0</strong>  1  0 = 1.
        Tento nov bit se vlo na zatek a registr se posune: 1 + 101 =
        1101.
      </p>
    </details>
    <p>
      <strong>2.</strong> Pokud je ifrovan text:
      1101100110101110011011111001101000001000 a nastaven ifry je m=4,
      poten vektor 1001 a koeficienty 1001, jak byl
      <strong>pvodn vstupn text (ve znacch)</strong>?
    </p>
    <details>
      <summary>Odpov 2</summary>
      <p><strong>Pvodn vstupn text:</strong> HELLO</p>
    </details>
  </div>
</template>

