/**
 * Caesarova ifra 
 */

const ALPHABET = "ABCDEFGHIJKLMNOPQRSTUVWXYZ";

/**
 * ENCRYPT
 * @param {string} text Oteven text
 * @param {number} shift kl
 * @returns {string} Zaifrovan text
 */
export function encrypt(text, shift) {
    let result = "";

    for (let char of text) {
        if (ALPHABET.includes(char)) {
            const oldIndex = ALPHABET.indexOf(char);
            // +26 a druh % 26 zajist sprvnou funkci i se zpornmi sly (pi deifrovn)
            const newIndex = ((oldIndex + shift) % 26 + 26) % 26;
            result += ALPHABET[newIndex];
        } else if (char === ' ' || char === '.') {
            result += char;
        }
    }

    return result;
}

/**
 * DECRYPT
 * @param {string} text Zaifrovan text
 * @param {number} shift kl
 * @returns {string} Deifrovan text
 */
export function decrypt(text, shift) {
    return encrypt(text, -shift);
}
