            <span id="polyalfabeticka" class="primary-color"
              >Polyalfabetick</span
            >: pokud symbol abecedy je mapovn na jeden z nkolika symbol<br />
            <span id="asymetricke" class="primary-color">Asymetrick ifry</span
            >: pouvaj se dva rzn kle - veejn kl pro ifrovn a
