<template>
  <main class="container flex">

    <!-- 1.st: VSTUP UIVATELE -->
    <section class="content flex">

      <!-- Komponenta pro nadpis -->
      <CipherHeader title="Afinn ifra" />

      <!-- Formul pro nastaven parametr -->
      <form class="params flex">
        <label :class="{ 'warning-color': !jeAckoNesoudelne }"
          >Hodnota A:</label
        >
        <input
          type="number"
          class="input"
          id="hodnotaA"
          v-model.number="hodnotaA"
          min="1"
          max="26"
          :class="{ 'invalid-border invalid-background': !jeAckoNesoudelne }"
          @keydown="blokujNeciselnePismena"
        />
        <label for="hodnotaB">Hodnota B:</label>
        <input
          type="number"
          class="input"
          id="hodnotaB"
          min="0"
          max="26"
          v-model.number="hodnotaB"
          @keydown="blokujNeciselnePismena"
        />
      </form>

      <!-- Komponenta pro textov vstup a vstup -->
      <CipherTextarea
        :vstupni-text="vstupniText"
        :vystupni-text="vystupniText"
      />

      <!-- Komponenta pro oznmen spnho zkoprovn textu (zptn vazba) -->
      <CipherToast :show="zobrazitToast" />

      <!-- Komponenta pro tlatka -->
      <CipherButtons 
        :disable-encrypt="!jeAckoNesoudelne"
        :disable-decrypt="!jeAckoNesoudelne"
      />

      <!-- Oznamen uivateli, e parametr A nen validn (zptn vazba)  -->
      <div v-if="!jeAckoNesoudelne">
        <p class="warning-color">ifrovn a deifrovn nen k dispozici!<br>Hodnota A mus bt nesoudln s 26</p>
      </div>

    </section>

    <!-- 2.st: VIZUALIZACE -->
    <aside class="afinne secondary-content flex">

      <!-- Vzorce pro ifrovn a deifrovn -->
      <div class="secondary-content-info flex column zero-gap">
        <div class="afinne viz-step">
          <h3>Vzorec pro ifrovn</h3>
          <div class="box-formula"><strong>C = (A  P + B) MOD 26</strong></div>
          <p><strong>C</strong> je index ifrovanho psmene</p>
          <p><strong>A</strong> je kl ifry (nesoudln s 26)</p>
          <p><strong>P</strong> je index pvodnho psmene</p>
          <p><strong>B</strong> je posun (0 a 26)</p>
        </div>
        <div class="afinne viz-step">
          <h3>Vzorec pro deifrovn</h3>
          <div class="box-formula">
            <strong>P = (A  (C - B)) MOD 26</strong>
          </div>
          <p><strong>P</strong> je index pvodnho psmene</p>
          <p><strong>A</strong> je inverzn hodnota A mod 26</p>
          <p><strong>C</strong> je index ifrovanho psmene</p>
          <p><strong>B</strong> je posun (0 a 26)</p>
        </div>
      </div>

      <!-- Tabulka aktulnho ifrovn znak -->
      <div class="affine-table">
        <table class="neutral-background">
          <thead>
            <tr>
              <th>Index</th>
              <th>Pvodn P</th>
              <th>ifrovan C</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(pismeno, index) in abeceda" :key="pismeno">
              <td>{{ index }}</td>
              <td>{{ pismeno }}</td>
              <td
                :class="{
                  'primary-color bold': jeIndexVPoliZvyraznench(index),
                  'warning-color bold': !jeAckoNesoudelne,
                }"
              >
                {{ ziskatAfinniPismeno(index) }}
              </td>
            </tr>
          </tbody>
        </table>
      </div>

    </aside>

    <!-- Komponenta pro zobrazen informac o ife -->
    <CipherInfo :show="zobrazitInfo">
      <AffineInfoContent />
    </CipherInfo>

  </main>
</template>

<script>
import { encrypt, decrypt } from '@/utils/ciphers/affine';
import AffineInfoContent from './components/AffineInfoContent.vue';
export default {
  name: "AfinneView",
  components: {
    AffineInfoContent
  },
  data() {
    return {
      vstupniText: "",
      hodnotaA: 1,
      hodnotaB: 0,
      vystupniText: "",
      abeceda: "ABCDEFGHIJKLMNOPQRSTUVWXYZ".split(""),
      zvyrazneneIndexy: [],
      zobrazitInfo: false,
      zobrazitToast: false,
    };
  },
  methods: {
    async sifrovat() {
      try {
        this.vystupniText = encrypt(this.vstupniText, this.hodnotaA, this.hodnotaB);
      } catch (error) {
        console.error("Dolo k chyb pi ifrovn:", error);
        alert(error.message || "Chyba pi ifrovn, zkontrolujte parametry");
      }
    },
    async desifrovat() {
      try {
        this.vystupniText = decrypt(this.vstupniText, this.hodnotaA, this.hodnotaB);
      } catch (error) {
        console.error("Dolo k chyb pi deifrovn:", error);
        alert(error.message || "Chyba pi deifrovn, zkontrolujte parametry");
      }
    },
    kopirovat(text) {
      if (text !== "") {
        navigator.clipboard.writeText(text);
        this.zobrazitToast = true;
        setTimeout(() => {
          this.zobrazitToast = false;
        }, 2000);
      }
    },
    vymazatVse() {
      this.vstupniText = "";
      this.vystupniText = "";
      this.hodnotaA = 1;
      this.hodnotaB = 0;
    },
    blokujNeciselnePismena(udalost) {
      const zakazaneKlavesy = ['e', 'E', '+', '-', '.', ','];
      if (zakazaneKlavesy.includes(udalost.key)) {
        udalost.preventDefault();
      }
    }, 
    // zvraznn textu v tabulce pi zmn parametr
    zmenitZvyrazneni() {
      for (let i = 0; i < this.abeceda.length; i++) {
        this.zvyrazneneIndexy.push(i);
      }
      setTimeout(() => {
        this.zvyrazneneIndexy = [];
      }, 600);
    },
    jeIndexVPoliZvyraznench(i) {
      for (let j = 0; j < this.zvyrazneneIndexy.length; j++) {
        if (this.zvyrazneneIndexy[j] === i) {
          return true;
        }
      }
    },
    // vpoet ifrovanho znaku podle indexu znaku otevenho textu v abeced
    ziskatAfinniPismeno(index) {
      const kod = (this.hodnotaA * index + this.hodnotaB) % 26;
      return this.abeceda[kod];
    },
    inverzniA() {
      for (let x = 1; x < 26; x++) {
        if ((this.hodnotaA * x) % 26 === 1) {
          return x;
        }
      }
      return null;
    },
  },
  // WATCH: sleduje zmny konkrtn reaktivn promnn a spust funkci, kdy se zmn
  watch: {
    hodnotaB() {
      if (this.hodnotaB < 0) {
        this.hodnotaB = 0;
      }
      if (this.hodnotaB > 26) {
        this.hodnotaB = 26;
      }
      this.zmenitZvyrazneni();
    },
    hodnotaA() {
      if (this.hodnotaA < 0) {
        this.hodnotaA = 0;
      }
      if (this.hodnotaA > 25) {
        this.hodnotaA = 25;
      }
      this.zmenitZvyrazneni();
    }
  },
  // COMPUTED: vypotan hodnota, kter se automaticky pepot, jen kdy se zmn data, na kterch zvis
  computed: {
    // je nesoudln == m inverzi
    jeAckoNesoudelne() {
      const inverze = this.inverzniA();
      if (inverze === null) {
        return false;
      } else {
        return true;
      }
    },
  },
};
</script>

<style scoped>
@import "@/assets/styles/cipher/afinne.css";
</style>
