<template>
  <main class="container flex">

    <!-- 1.st: VSTUP UIVATELE -->
    <section class="content flex">

      <!-- Komponenta pro nadpis -->
      <CipherHeader title="Substitun ifra" />

      <!-- Formul pro nastaven parametr -->
      <form class="params flex">
        <label :class="{ 'warning-color': jePermutaceNeplatna }">
          Permutace abecedy:
          <div class="flex" style="margin-top: 5px">
            <input
              v-model="permutace"
              type="text"
              class="input-permutation"
              :class="{
                'invalid-background invalid-border': jePermutaceNeplatna,
              }"
              maxlength="26"
              @input="filtrovatPermutaci"
            />
            <button
              type="button"
              class="btn-view neutral-color center"
              @click="generovatNahodnouPermutaci"
              style="margin-left: 10px"
            >
              Nhodn
            </button>
          </div>
        </label>
      </form>

      <!-- Komponenta pro textov vstup a vstup -->
      <CipherTextarea
        :vstupni-text="vstupniText"
        :vystupni-text="vystupniText"
      />

      <!-- Komponenta pro oznmen spnho zkoprovn textu (zptn vazba) -->
      <CipherToast :show="zobrazitToast" />

      <!-- Komponenta pro tlatka -->
      <CipherButtons 
        :disable-encrypt="jePermutaceNeplatna"
        :disable-decrypt="jePermutaceNeplatna"
      />

      <!-- Oznamen uivateli, e permutace nen validn (zptn vazba) -->
      <div v-if="jePermutaceNeplatna">
        <p class="warning-color">ifrovn a deifrovn nen k dispozici!<br> Permutace je neplatn</p>
      </div>

    </section>

    <!-- 2.st: VIZUALIZACE -->
    <aside class="substitution secondary-content flex column">
      
      <!-- Tabulka aktulnho ifrovn znak -->
      <section>
        <table class="substitution-table neutral-background">
          <thead>
            <tr>
              <th v-for="pismeno in abeceda" :key="'orig-' + pismeno">
                {{ pismeno }}
              </th>
            </tr>
          </thead>
          <tbody
            :class="[
              {
                'warning-color bold': jeCervena,
                'primary-color bold': jeZelena,
              },
            ]"
          >
            <tr>
              <td
                v-for="(pismeno, index) in permutace.split('')"
                :key="'perm-' + index"
              >
                {{ pismeno }}
              </td>
            </tr>
          </tbody>
        </table>
      </section>

      <!-- Popis ifrovn a deifrovn -->
      <section class="substitution viz-step">
        <h3>Popis</h3>
        <p>
          Kad psmeno ve vstupnm textu je nahrazeno psmenem na stejnm mst
          v permutaci abecedy. Napklad, pokud je permutace abecedy:
          <a
            ><strong>{{ permutace }}</strong></a
          >, pak psmeno <strong>A</strong> bude nahrazeno
          <strong>{{ permutace.charAt(0) }}</strong
          >, <strong>B</strong> bude nahrazeno
          <strong>{{ permutace.charAt(1) }}</strong
          >, a tak dle.
        </p>
        <p>
          Pro deifrovn se proces obrt: kad psmeno ve ifrovanm textu je
          nahrazeno psmenem na stejnm mst v pvodn abeced.
        </p>
      </section>

    </aside>

    <!-- Komponenta pro zobrazen informac o ife -->
    <CipherInfo :show="zobrazitInfo">
      <SubstitutionInfoContent />
    </CipherInfo>

  </main>
</template>

<script>
import { encrypt, decrypt } from '@/utils/ciphers/substitution';
import SubstitutionInfoContent from './components/SubstitutionInfoContent.vue';
export default {
  name: "SubstitutionView",
  components: {
    SubstitutionInfoContent
  },
  data() {
    return {
      vstupniText: "",
      vystupniText: "",
      permutace: "ABCDEFGHIJKLMNOPQRSTUVWXYZ",
      abeceda: "ABCDEFGHIJKLMNOPQRSTUVWXYZ".split(""), 
      zobrazitInfo: false,
      zobrazitToast: false,
      jeCervena: false,
      jeZelena: false,
    };
  },
  methods: {
    async sifrovat() {
      try {
        this.vystupniText = encrypt(this.vstupniText, this.permutace);
      } catch (error) {
        console.error("Dolo k chyb pi ifrovn:", error);
        alert("Chyba pi ifrovn, zkontrolujte parametry");
      }
    },
    async desifrovat() {
      try {
        this.vystupniText = decrypt(this.vstupniText, this.permutace);
      } catch (error) {
        console.error("Dolo k chyb pi deifrovn:", error);
        alert("Chyba pi deifrovn, zkontrolujte parametry");
      }
    },
    kopirovat(text) {
      if (text !== "") {
        navigator.clipboard.writeText(text);
        this.zobrazitToast = true;
        setTimeout(() => {
          this.zobrazitToast = false;
        }, 2000);
      }
    },
    vymazatVse() {
      this.vstupniText = "";
      this.vystupniText = "";
      this.jeCervena = false;
      this.jeZelena = false;
    },
    filtrovatPermutaci() {
      const filtrovanaHodnota = this.permutace.replace(/[^a-zA-Z]/g, "").toUpperCase();
      this.permutace = filtrovanaHodnota;
    },
    generovatNahodnouPermutaci() {
      this.permutace = this.permutace.split("").sort(() => Math.random() -0.5).join("");
    }
  },
  // WATCH: sleduje zmny konkrtn reaktivn promnn a spust funkci, kdy se zmn
  watch: {
    permutace() {
      if (this.jePermutaceNeplatna) {
        this.jeCervena = true;
        this.jeZelena = false;
      } else {
        this.jeCervena = false;
        this.jeZelena = true;

        setTimeout(() => {
          this.jeZelena = false;
        }, 600);
      }
    },
  },
  // COMPUTED: vypotan hodnota, kter se automaticky pepot, jen kdy se zmn data, na kterch zvis
  computed: {
    jePermutaceNeplatna() {
      if (this.permutace.length !== 26) {
        return true;
      }

      let pocetUnikatnich = 0;
      const pismena = {};

      for (let i = 0; i < this.permutace.length; i++) {
        let pismeno = this.permutace[i];
        if (!pismena[pismeno]) {
          pismena[pismeno] = true;
          pocetUnikatnich = pocetUnikatnich + 1;
        }
      }

      if (pocetUnikatnich !== 26) {
        return true;
      }

      return false;
    },
  },
};
</script>

<style scoped>
@import "@/assets/styles/cipher/substitution.css";
</style>
