<template>
  <div>
    <h3 id="info-title">O RSA</h3>
    <p>
      RSA ifra je jednou z nejznmjch a nejpouvanjch
      <router-link to="/#asymetricke" class="primary-color without-underline">
        asymetrickch ifer</router-link
      >. Byla vyvinuta Ronem Rivestem, Adi Shamirem a Leonardem Adlemanem v
      roce 1977. Je pojmenovna podle prvnch psmen jejich pjmen.
      <router-link to="/#rsa" class="primary-color without-underline">
        RSA</router-link
      >
      se iroce pouv pro zabezpeen dat a pro digitln podpisy.
    </p>
    <h4>Popis</h4>
    <p>
      RSA je asymetrick ifra, co znamen, e pouv dva rzn kle:
      <strong>Veejn kl (e, n)</strong> - slou k ifrovn zprv, me
      bt voln sdlen <strong>Soukrom kl (d, n)</strong> - slou k
      deifrovn zprv, mus zstat tajn
    </p>
    <h5>Parametry RSA</h5>
    <p>p, q - dv rzn prvosla (mus bt tajn)<br>
    n = p  q - modul, souin prvosel (veejn)<br>
    (n) = (p-1)  (q-1) - Eulerova funkce (tajn)<br>
    e - veejn exponent, plat: nsd(e, (n)) = 1<br>
    d - soukrom exponent, plat: (e  d) mod (n) = 1</p>
    <h5>Postup ifrovn</h5>
    <ol>
      <li>Peve kad znak na slo (ASCII): m = znak</li>
      <li>
        Zaifruj:
        <span
          style="
            white-space: pre-wrap;
            font-size: 0.9em;
            font-weight: bold;
            color: #36a06f;
          "
        >
          c = m<sup>e</sup> mod n</span
        >
      </li>
      <li>Vsledek jsou zaifrovan sla oddlen mezerami</li>
    </ol>

    <h5>Postup deifrovn</h5>
    <ol>
      <li>Vezmi zaifrovan slo c</li>
      <li>
        Deifruj:<span
          style="
            white-space: pre-wrap;
            font-size: 0.9em;
            font-weight: bold;
            color: #36a06f;
          "
        >
          m = c<sup>d</sup> mod n</span
        >
      </li>
      <li>Peve slo zpt na znak (ASCII)</li>
    </ol>

    <h4>Bezpenost</h4>
    <p>
      Bezpenost RSA je zaloena na
      <router-link
        to="/#obtiznost-faktorizace"
        class="primary-color without-underline"
      >
        obtnosti faktorizace velkch sel.</router-link
      >
      I kdy tonk zn veejn kl (e, n), neme jednodue vypotat
      soukrom kl (d,n), protoe by musel rozloit n na prvosla p a q. Pro
      velk a co nejmn podobn p a q je povaovna za bezpenou a v praxi
      pouvanou. (V naem ppad se pro ukzku spokojme s malmi p a q)
    </p>
    <h4>Vce informac</h4>
    <a
      href="https://en.wikipedia.org/wiki/RSA_cipher"
      target="_blank"
      class="primary-color without-underline"
      >RSA cipher - Wikipedia</a
    >
    <h4>Test</h4>
    <p>
      <strong>1.</strong> Mjme prvosla p=13 a q=11. Jak bude
      <strong>n</strong> a <strong>(n)</strong>?
    </p>
    <details>
      <summary>Odpov 1</summary>
      <p>n = p  q = 13  11 = <strong>143</strong></p>
      <p>(n) = (p-1)  (q-1) = 12  10 = <strong>120</strong></p>
    </details>
    <p>
      <strong>2.</strong> Jak bude ifrovan hodnota znaku
      <strong>E</strong>, kdy veejn kl = (5, 91)?
    </p>
    <details>
      <summary>Odpov 2</summary>
      <p>Veejn kl (e=5, n=91) pouijeme k ifrovn.</p>
      <p><strong>Postup:</strong></p>
      <ol>
        <li>Pevedeme znak "E" na ASCII hodnotu: m = 69</li>
        <li>
          Pouijeme vzorec: <br />c = m<sup>e</sup> mod n = 69<sup>5</sup> mod 91 = <strong>29</strong>
        </li>
      </ol>
      <p>ifrovan hodnota je <strong>29</strong></p>
    </details>
  </div>
</template>

<script>
export default {
  name: 'RsaInfoContent'
}
</script>
